import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.christiano.bolla.pages.HomePage() }
        ctx.router.register("/projects") { com.christiano.bolla.pages.ProjectsPage() }
        ctx.router.register("/services") { com.christiano.bolla.pages.ServicesPage() }
        ctx.router.register("/services/{serviceId}") { com.christiano.bolla.pages.ServicePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerComponentStyle(com.christiano.bolla.pages.ProjectTagLinkStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.AboutImageStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.AboutTextStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.BackToTopButtonStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.InputStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.NavigationItemStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.LogoStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.SocialLinkStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.MainButtonStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.MainImageStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.PortfolioSectionStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.PortfolioArrowIconStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.ProjectNameStyle)
        ctx.theme.registerComponentStyle(com.christiano.bolla.styles.ServicesTypeCardStyle)
        ctx.theme.registerComponentVariants(com.christiano.bolla.components.PrimaryButtonVariant)
        ctx.theme.registerComponentVariants(com.christiano.bolla.components.NormalButtonVariant)
        ctx.theme.registerComponentVariants(com.christiano.bolla.components.TextPrimaryButtonVariant)
        com.christiano.bolla.updateTheme(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(AppGlobalsLocal provides mapOf("title" to "Portfolio Web Compose",
                "BASE_URL" to "")) {
            com.christiano.bolla.MyApp {
                router.renderActivePage { renderWithDeferred { it() } }
            }
        }
    }
}
